App {
  margin: 0;
  padding: 0;
}

.App-header {
  display: -ms-flexbox;
  display: flex;
  color: #df3148;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: thick solid #df3148;
}

.App-header h1 {
  font-size: 4rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
}
.App-body {
  font-size: 1.5rem;
  padding: 25px;
}
.App-footer { 
  font-size: 1.5rem;
  width: 100%;
  position: absolute;
  bottom: 0;
  text-align: center;
  border-top: thick solid #df3148;
}

.App-body .form label {
  padding: 5px;
  
}
.App-body .form input {
  padding: 5px;
}


